import {Component, OnInit} from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent implements OnInit {
  homePrice = 3750000;
  creditPrice = 3000000;

  percent = 8.4;

  years = 15;
  months = 180;

  firstPaymentPercent = 15;
  firstPayment = 750000;

  payBeforeFrom = 1;
  payBeforeTo = this.months;
  payBeforeSum = 35000;

  monthPayment: number;
  totalPayment: number;
  totalPercentagePayment: number;
  totalPercentageEconomyPayment: number;
  totalEconomy: number;
  totalEconomyMonths = 0;
  totalEconomyPayment = 0;

  totalResultPerMonth: IResultItem[] = [];

  private static getMonthPayment(fullDebt: number, percentPerMonth: number, months: number): number {
    let monthPayment =  fullDebt * ((percentPerMonth * Math.pow(1 + percentPerMonth, months))
      / (Math.pow(1 + percentPerMonth, months) - 1));

    // monthPayment = Math.ceil(monthPayment / 1000) * 1000;

    return monthPayment;
  }

  ngOnInit(): void {
    this.calc();
  }

  calc() {
    const percentPerMonth = this.percent / 100 / 12;
    this.monthPayment = AppComponent.getMonthPayment(this.creditPrice, percentPerMonth, this.months);
    this.totalPayment = (this.monthPayment * this.months);
    this.totalPercentagePayment = this.totalPayment - this.creditPrice;
    this.totalResultPerMonth = [];
    this.totalEconomyPayment = 0;

    let monthPayment = this.monthPayment;
    let tempPercentage = 0;
    let tempDebt = 0;
    let tempFullDebt = this.creditPrice;
    let monthPaymentSum = 0;
    let payBeforeSum = 0;
    let needBreak = false;

    for (let i = 1; i <= this.months; i++) {
      monthPayment = AppComponent.getMonthPayment(tempFullDebt, percentPerMonth, this.months - i + 1);

      if (i >= this.payBeforeFrom && i <= this.payBeforeTo) {
        payBeforeSum = this.payBeforeSum - monthPayment;
        monthPaymentSum = this.payBeforeSum;
      } else {
        payBeforeSum = 0;
        monthPaymentSum = monthPayment;
      }

      tempPercentage = tempFullDebt * this.percent * 0.01 / 12;

      if (monthPaymentSum > tempFullDebt + tempPercentage) {
        monthPaymentSum = tempFullDebt + tempPercentage;
        payBeforeSum = monthPaymentSum - monthPayment - tempPercentage;
        needBreak = true;
      }

      tempDebt = monthPaymentSum - tempPercentage;
      tempFullDebt -= tempDebt;

      this.totalEconomyPayment += monthPaymentSum;
      this.totalResultPerMonth.push({
        month: i,
        toDebt: tempDebt,
        toPercent: tempPercentage,
        debt: tempFullDebt,
        payBeforeSum,
        monthPayment
      });

      if (needBreak) {
        this.totalEconomyMonths = this.months - i;
        break;
      }
    }

    this.totalPercentageEconomyPayment = this.totalPayment - this.totalEconomyPayment;
  }

  onHomePriceChanged(newValue) {
    this.homePrice = +newValue;

    if (this.firstPaymentPercent) {
      this.firstPayment = (this.homePrice / 100) * this.firstPaymentPercent;
      this.creditPrice = this.homePrice - this.firstPayment;
    }
  }

  onCreditPriceChanged(newValue) {
    this.creditPrice = +newValue;

    if (this.firstPaymentPercent) {
      this.homePrice = (this.creditPrice / (100 - this.firstPaymentPercent)) * 100;
      this.firstPayment = (this.homePrice / 100) * this.firstPaymentPercent;
    }
  }

  onYearsChanged(newValue) {
    this.years = +newValue;
    this.months = this.years * 12;
  }

  onMonthsChanged(newValue) {
    this.months = +newValue;
    this.years = this.months / 12;
  }

  onFirstPaymentPercentChanged(newValue) {
    this.firstPaymentPercent = +newValue;

    if (this.homePrice) {
      this.firstPayment = (this.homePrice / 100) * this.firstPaymentPercent;
      this.creditPrice = this.homePrice - this.firstPayment;
    }
  }

  onFirstPaymentRubChanged(newValue) {
    this.firstPayment = +newValue;

    if (this.homePrice) {
      this.creditPrice = this.homePrice - this.firstPayment;
      this.firstPaymentPercent = this.firstPayment / (this.homePrice / 100);
    }
  }
}

interface IResultItem {
  month: number;
  monthPayment: number;
  toDebt: number;
  toPercent: number;
  debt: number;
  payBeforeSum: number;
}
